<template>filterast
  <div class="main_filterast" :style="screenWidth >= 768 ? {height: 'calc(100% + 300px)'} : {height: 'calc(100%)'}">
    <div class="arrowUp" @click="topFunction()" v-if="scrollTopVisible">    
    </div>
    <div class="filterlogo_filterast" v-if="screenWidth >= 768">
      <div class="arrowCharStyleFilterAst" @click="goBackChar()">
        <img alt="Go Back" class="arrowImageFilter" src="../assets/left_arrow.png" />
      </div>
      <div class="filterast_section_wrapper">
        <div id="filterast_section_effect">
          <h3 class="filterast_section_label">Character Info</h3>
          <div class="filterast_section_selects">
            <n-select
                v-model:value="selectedChar[0]"
                filterable
                placeholder="Character"
                :options=ast_char_arguments.char
                style="width: 120px"
                @update:value="handleUpdateSelect"
                clearable
                :consistent-menu-width="false"
              />
              <n-select
                v-model:value="selectedChar[1]"
                filterable
                placeholder="Stars"
                :options=ast_char_arguments.stars
                style="width: 120px;"
                @update:value="handleUpdateSelect"
                clearable
                :consistent-menu-width="false"
              />
              <n-select
                v-model:value="selectedChar[2]"
                filterable
                placeholder="Status Type"
                :options=ast_char_arguments.type_stat
                style="width: 140px;"
                @update:value="handleUpdateSelect"
                clearable
                :consistent-menu-width="false"
              />
            <n-select
                v-model:value="selectedChar[3]"
                filterable
                placeholder="Time Limited?"
                :options=ast_char_arguments.truefalse
                style="width: 140px;"
                @update:value="handleUpdateSelect"
                clearable
                :consistent-menu-width="false"
              />
            <n-select
                v-model:value="selectedChar[4]"
                filterable
                placeholder="Free Unit?"
                :options=ast_char_arguments.truefalse
                style="width: 140px;"
                @update:value="handleUpdateSelect"
                clearable
                :consistent-menu-width="false"
              />
            <n-select
              v-model:value="selectedChar[5]"
              filterable
              placeholder="Have Repeat?"
              :options=ast_char_arguments.truefalse
              style="width: 160px;"
              @update:value="handleUpdateSelect"
              clearable
              :consistent-menu-width="false"
            />
          </div>
        </div>
        <!-- style="height: 70px" -->
        <div id="filterast_section_effect" >
          <h3 class="filterast_section_label">Effect Info</h3>
          <div class="filterast_section_selects" >
            <n-select
              v-model:value="selectedEffects[0]"
              filterable
              placeholder="Effects Foes"
              :options=ast_effects_arguments.targets
              style="width: 150px"
              @update:value="handleUpdateSelect"
              clearable
              :consistent-menu-width="false"
            />
            <n-select
              v-model:value="selectedEffects[1]"
              filterable
              placeholder="Effects"
              :options=ast_effects_arguments.effects
              style="width: 150px"
              @update:value="handleUpdateSelect"
              clearable
              :consistent-menu-width="false"
            />
            <n-input-number 
              placeholder="Val Min"
              :step="5"
              min = "-150"
              max = "150"
              v-model:value="selectedEffects[2]"
              style="width: 180px"
              @update:value="handleUpdateSelect"
              clearable 
            >
              <template #suffix>
                <div v-if="effectTurns">
                  &#xB1;
                </div>
                <div v-else>
                  %
                </div>
              </template>
            </n-input-number>
            <n-input-number 
              placeholder="Val Max"
              :step="5"
              min = "-150"
              max = "150"
              v-model:value="selectedEffects[3]"
              style="width: 180px"
              @update:value="handleUpdateSelect"
              clearable 
            >
              <template #suffix>
                <div v-if="effectTurns">
                  &#xB1;
                </div>
                <div v-else>
                  %
                </div>
              </template>
            </n-input-number>
            <n-select
              v-model:value="selectedRepeat[0]"
              filterable
              placeholder="Nº Repeat"
              :options=ast_effects_arguments.repeat
              style="width: 140px"
              @update:value="handleUpdateSelect"
              clearable
              :consistent-menu-width="false"
            />
          </div>
          <n-switch
            :round="false"
            :default-value="false"
            v-model:value="effectLevel"
            @update:value="handleUpdateSelect"
            id="filterast_section_eflevel"
          >
            <template #checked>
              ++
            </template>
            <template #unchecked>
              +
            </template>
          </n-switch>
          <n-select
            v-model:value="selectedIncludeRepeat[0]"
            placeholder="FilterRepeat"
            :options=ast_effects_arguments.repeatorattack
            style="width: 130px"
            id="filterast_section_includeRepeat"
            @update:value="handleUpdateSelect"
            clearable
            :consistent-menu-width="false"
          />
        </div>
        <div id="filterast_section_effect" style="border-bottom: 0px; width: 60%; border-right: black 1px solid;">
          <h3 class="filterast_section_label">Extra:</h3>
          <div class="filterast_section_selects" style="width: 100%">
            <n-select
                v-model:value="sortBy"
                filterable
                placeholder="Sort by:"
                :options=skill_sort.sort
                style="width: 120px"
                @update:value="handleUpdateSelect"
                clearable
                :consistent-menu-width="false"
              />
              <n-select
                v-model:value="sortAsc"
                filterable
                placeholder="Order"
                :options=skill_sort.asc
                style="width: 120px; margin-right: 20px;"
                @update:value="handleUpdateSelect"
                clearable
                :consistent-menu-width="false"
              />
              <n-select
                v-model:value="displayInfo"
                filterable
                placeholder="Display Info"
                :options=skill_sort.display
                style="width: 140px"
                @update:value="updateDisplay"
                clearable
                :consistent-menu-width="false"
              />
          </div>
        </div>
      </div>
      <div class="filter_section_button">
        <!-- <h3 style="width: 100%">Sort by</h3> -->
        <n-button 
          type="error"
          style="width: 30%; height: 64px"
          @click="clearFilter()"
        >
          Reset
        </n-button>
        <n-button 
          type="primary"
          style="width: 68%; margin-left: 2%; height: 64px"
          @click="searchFilter()"
        >
          Search
        </n-button>
      </div>
    </div>
    <div class="filterast_logo_modal" v-else>
      <n-button id="filter_modal_button" @click="filterModal = true" type="primary">Open Filter</n-button>
      <n-modal v-model:show="filterModal">
        <n-card
          style="position:absolute; top: 8%; width: 99%; left: 0.5%; height: 75%; overflow: auto;"
          :bordered="false"
          size="huge"
          role="dialog"
          aria-modal="true"
        >
          <template #header-extra>
            <n-button @click="filterModal = false" type="error">X</n-button>
          </template>
          <n-scrollbar class="filterast_modal_section_wrapper" trigger="none">
            <div class="filterast_modal_section_char">
              <h3 style="width: 100%">Character Info</h3>
              <n-select
                v-model:value="selectedChar[0]"
                filterable
                placeholder="Character"
                :options=ast_char_arguments.char
                style="width: 49%"
                @update:value="handleUpdateSelect"
                clearable
                :consistent-menu-width="false"
              />
              <n-select
                v-model:value="selectedChar[1]"
                filterable
                placeholder="Stars"
                :options=ast_char_arguments.stars
                style="width: 49%;"
                @update:value="handleUpdateSelect"
                clearable
                :consistent-menu-width="false"
              />
              <n-select
                v-model:value="selectedChar[2]"
                filterable
                placeholder="Status Type"
                :options=ast_char_arguments.type_stat
                style="width: 49%;"
                @update:value="handleUpdateSelect"
                clearable
                :consistent-menu-width="false"
              />
            <n-select
                v-model:value="selectedChar[3]"
                filterable
                placeholder="Time Limited?"
                :options=ast_char_arguments.truefalse
                style="width: 49%;"
                @update:value="handleUpdateSelect"
                clearable
                :consistent-menu-width="false"
              />
            <n-select
                v-model:value="selectedChar[4]"
                filterable
                placeholder="Free Unit?"
                :options=ast_char_arguments.truefalse
                style="width: 49%;"
                @update:value="handleUpdateSelect"
                clearable
                :consistent-menu-width="false"
              />
            <n-select
              v-model:value="selectedChar[5]"
              filterable
              placeholder="Have Repeat?"
              :options=ast_char_arguments.truefalse
              style="width: 49%;"
              @update:value="handleUpdateSelect"
              clearable
              :consistent-menu-width="false"
            />
            </div>
            <div class="filterast_modal_section_char">  
              <h3 style="width: 100%">Effect Info</h3>
              <n-select
                v-model:value="selectedEffects[0]"
                filterable
                placeholder="Effects Foes"
                :options=ast_effects_arguments.targets
                style="width: 49%"
                @update:value="handleUpdateSelect"
                clearable
                :consistent-menu-width="false"
              />
              <n-select
                v-model:value="selectedEffects[1]"
                filterable
                placeholder="Effects"
                :options=ast_effects_arguments.effects
                style="width: 49%"
                @update:value="handleUpdateSelect"
                clearable
                :consistent-menu-width="false"
              />
              <div class="filterast_modal_section_effect_select">
                <n-input-number 
                  placeholder="Min. Value"
                  :step="5"
                  min = "-150"
                  max = "150"
                  v-model:value="selectedEffects[2]"
                  style="width: 49%"
                  @update:value="handleUpdateSelect"
                  clearable 
                >
                <template #suffix>
                  <div v-if="effectTurns">
                    &#xB1;
                  </div>
                  <div v-else>
                    %
                  </div>
                </template>
                </n-input-number>
                <n-input-number 
                  placeholder="Max. Value"
                  :step="5"
                  min = "-150"
                  max = "150"
                  v-model:value="selectedEffects[3]"
                  style="width: 49%"
                  @update:value="handleUpdateSelect"
                  clearable 
                >
                  <template #suffix>
                    <div v-if="effectTurns">
                      &#xB1;
                    </div>
                    <div v-else>
                      %
                    </div>
                  </template>
                </n-input-number>
              </div>
              <n-select
                v-model:value="selectedRepeat[0]"
                filterable
                placeholder="Nº Repeat"
                :options=ast_effects_arguments.repeat
                style="width: 49%"
                @update:value="handleUpdateSelect"
                clearable
                :consistent-menu-width="false"
              />
              <div style="width: 100%; margin-top: 14px; display: flex; align-items: center; justify-content: center;">
                <n-select
                  v-model:value="selectedIncludeRepeat[0]"
                  placeholder="FilterRepeat"
                  :options=ast_char_arguments.truefalse
                  style="width: 49%"
                  @update:value="handleUpdateSelect"
                  clearable
                  :consistent-menu-width="false"
                />
                <n-switch
                  :round="false"
                  :default-value="false"
                  v-model:value="effectLevel"
                  @update:value="handleUpdateSelect"
                  style="margin-left: 10px"
                >
                  <template #checked>
                    ++
                  </template>
                  <template #unchecked>
                    +
                  </template>
                </n-switch>
              </div>
            </div>
            <div class="filterast_modal_section_char">
              <h3 style="width: 100%">Extra Info:</h3>
              <n-select
                v-model:value="sortBy"
                filterable
                placeholder="Sort by:"
                :options=skill_sort.sort
                style="width: 49%"
                @update:value="handleUpdateSelect"
                clearable
                :consistent-menu-width="false"
              />
              <n-select
                v-model:value="sortAsc"
                filterable
                placeholder="Order"
                :options=skill_sort.asc
                style="width: 49%"
                @update:value="handleUpdateSelect"
                clearable
                :consistent-menu-width="false"
              />
              <n-select
                v-model:value="displayInfo"
                filterable
                placeholder="Display Info"
                :options=skill_sort.display
                style="width: 98%"
                @update:value="updateDisplay"
                clearable
                :consistent-menu-width="false"
              />
            </div>
            <div class="filterast_modal_section_button">
              <n-button 
                type="error"
                style="width: 30%; height: 80px"
                @click="clearFilter()"
              >
                Reset
              </n-button>
              <n-button 
                type="primary"
                style="width: 68%; margin-left: 2%; height: 80px"
                @click="searchFilter()"
              >
                Search
              </n-button>
            </div>
          </n-scrollbar>
        </n-card>
      </n-modal>
    </div>
    <n-scrollbar class="main_outlist" trigger="none" :style="screenWidth >= 768 ? {height: 'calc(100% - 660px)'} : {height: 'calc(100% - 240px)'}">
      <div class="main_inlist">
        <div v-if="dataAst.length == 0">
          <p>
            Loading...
          </p>
        </div>
        <div class="list_empty_message" v-if="filteredData.length == 0 && !isFirstSearch">
          <h3>
            So empty...
          </h3>
          <p>
            This search found nothing
          </p>
        </div>
        <n-grid :x-gap="0" :y-gap="0" :cols="n_columns" >
          <n-grid-item v-for="char in filteredData" :key="char.id_order" class="cell">
            <div class="imagecell" @click='gotoCharPage(char.id_adventurers)' :title="'[' + char.title + '] ' + char.name">
              <img alt="Frame" :src="decideBase(char.stars)" >
              <!-- <img :alt="char.id_intern" :src="getImg(char.id_intern)" class="char_img"> -->
              <img alt="Frame_front" :src="decideBaseFrame(char.stars)">
              <div class="display_info" v-if="showDisplay">
                <span style="color: white">{{ getDisplay(char) }}</span> 
              </div>
            </div>
          </n-grid-item>
        </n-grid>
      </div>
    </n-scrollbar>
  </div>
</template>

<script>
import { 
  NScrollbar, 
  NGrid, 
  NGridItem, 
  NButton, 
  NTooltip,
  NSelect,
  NCheckbox,
  NSpace,
  NSwitch,
  NInputNumber,
  NModal,
  NInput,
  NCard
} from 'naive-ui'
import axios from 'axios'
import {ref} from 'vue'

export default {
  components: { 
    NScrollbar,
    NGrid, 
    NGridItem, 
    NButton, 
    NTooltip,
    NSelect,
    NCheckbox,
    NSpace,
    NSwitch,
    NInputNumber,
    NInput,
    NModal,
    NCard
  },
  data() {
    return {
      dataAst: [],
      filteredData: [],

      isFirstSearch: true,

      filterModal: ref(null),
      // Char, Stars, Type Stat, TimeLimited, isFree, Have Repeat
      selectedChar: ref([null, null, null, null, null, null]),
      // Target, effect, value1, value2
      selectedEffects: ref([null, null, null, null]),
      // N_repeat
      selectedRepeat: ref([null]),
      // Include repeat on search
      selectedIncludeRepeat: ref([null]),
      effectLevel: ref(false),

      sortBy: ref(null),
      sortAsc: ref(null),

      displayInfo: ref(null),
      showDisplay: false,

      effectTurns: false,

      scrollTopVisible: false,

      screenWidth: window.innerWidth,
      displayColor: "",

      n_columns: 9,

      ast_char_arguments: null,
      ast_effects_arguments: null,
      skill_sort: null,
    }
  },
  beforeMount() {
    var ast_char = require('../composables/constants/filter_ast_char.json')
    var ast_effects = require('../composables/constants/filter_ast_effects.json')
    var ast_sort = require('../composables/constants/filter_ast_sort.json')

    let ast_list = window.sessionStorage.getItem("ast_list")
    if(ast_list == null){
      var data = require('../data/ast_list_DATA.json')
      this.dataAst = data
      this.filteredData = data
      window.sessionStorage.setItem("ast_list", JSON.stringify(data));

    }else{
      this.dataAst = JSON.parse(ast_list)
      this.filteredData = JSON.parse(ast_list)
    }

    let query = this.$route.query
    if(Object.keys(query).length != 0){
      this.isFirstSearch = false

      // Char
      this.selectedChar[0] = query.char
      this.selectedChar[1] = query.star
      this.selectedChar[2] = query.status
      this.selectedChar[3] = query.timelimited
      this.selectedChar[4] = query.free
      this.selectedChar[5] = query.repeat
      // Effect
      this.selectedEffects[0] = query.ef_target
      this.selectedEffects[1] = query.ef
      this.selectedEffects[2] = query.ef_val1 ? parseInt(query.ef_val1) : null
      this.selectedEffects[3] = query.ef_val2 ? parseInt(query.ef_val2) : null
      // Repeat
      this.selectedRepeat[0] = query.n_repeat

      this.selectedIncludeRepeat[0] = query.filter_repeat_effect
      this.effectLevel = query.effect_lv ? true : false

      this.sortBy = query.sort
      this.sortAsc = query.asc
      this.displayInfo = query.display
    }

    if(query.display != null){
      this.showDisplay = true;
    }

    this.ast_char_arguments = ast_char
    this.ast_effects_arguments = ast_effects
    this.skill_sort = ast_sort
    
    this.onResize()
    this.searchFilter()
  },
  mounted() {
    this.$nextTick(() => {
      window.addEventListener('resize', this.onResize);
      // window.addEventListener('scroll', this.scrollListener)
    })
  },

  beforeDestroy() { 
    window.removeEventListener('resize', this.onResize); 
    // window.removeEventListener('scroll', this.scrollListener)
  },
  methods: {
    onResize() {
      this.screenWidth = window.innerWidth
      if(this.screenWidth > 1000){
        this.n_columns = 9
        this.filterModal = false
      }else if(this.screenWidth > 785){
        this.n_columns = 7
        this.filterModal = false
      }else if(this.screenWidth > 600){
        this.n_columns = 5
      }else{
        this.n_columns = 3
      }
    },
    // scrollListener(e) {
    //   this.scrollTopVisible = window.scrollY > 150
    // },
    updateDisplay(){
      if(this.displayInfo != null){
        this.showDisplay = true
        this.displayColor = this.displayInfo
      }else{
        this.showDisplay = false
      }
      this.handleUpdateSelect()
    },
    getDisplay(char){
      let query = this.$route.query;
      if(query.display && char){
        switch(query.display){
          case "char_stat":
            return this.decideStat(char.type_stat)
          case "char_stars":
            return char.stars
          case "char_time_limited":
            return char.time_limited ? "Yes" : "No"
          case "char_free_unit":
            return char.is_free ? "Yes" : "No"
          case "char_has_repeat":
            return char.have_repeat ? "Yes" : "No"
          case "char_str":
            return char.str
          case "char_end":
            return char.end
          case "char_dex":
            return char.dex
          case "char_agi":
            return char.agi
          case "char_mag":
            return char.mag
          case "char_hp":
            return char.hp
          case "char_mp":
            return char.mp
          case "char_max_str":
            return char.max_str
          case "char_max_end":
            return char.max_end
          case "char_max_dex":
            return char.max_dex
          case "char_max_agi":
            return char.max_agi
          case "char_max_mag":
            return char.max_mag
          case "char_max_hp":
            return char.max_hp
          case "char_max_mp":
            return char.max_mp
          case "effect_value":
            return this.searchEffectValue(char, query)
          default:
            return char.name
        }
      }
    },
    decideStat(type_stat){
      switch(type_stat){
        case "str":
          return 'Strength'
        case "str_plus":
          return 'Strength'
        case "end":
          return 'Endurance'
        case "end_plus":
          return 'Endurance'
        case "dex":
          return 'Dexterity'
        case "dex_plus":
          return 'Dexterity'
        case "agi":
          return 'Agility'
        case "agi_plus":
          return 'Agility'
        case "mag":
          return 'Magic'
        case "mag_plus":
          return 'Magic'
        case "free":
          return 'Free'
        case "free_plus":
          return 'Free'
        default:
          return '???'
      }
    },
    searchEffectValue(char, query){
      if(query.ef){
        // let lowestValue = 200
        let highestValue = -200
        let highestString = ""

        if(Object.keys(char.effect).length != 0){
          for(let j = 0; j < char.effect.n_effects; j++){
            if(char.effect["level1_effect" + (j+1).toString() + "_attr"].toLowerCase().replace(/ /g, "_") == query.ef){
              if(query.ef_target){
                if(char.effect["level1_effect" + (j+1).toString() + "_target"].toLowerCase() == query.ef_target){
                  if(this.effectLevel == false){
                    if((parseInt(char.effect["level1_effect" + (j+1).toString() + "_mod"])) > highestValue){
                      highestValue = parseInt(char.effect["level1_effect" + (j+1).toString() + "_mod"])
                      highestString = char.effect["level1_effect" + (j+1).toString() + "_mod"]
                    }
                  }else{
                    if((parseInt(char.effect["level2_effect" + (j+1).toString() + "_mod"])) > highestValue){
                      highestValue = parseInt(char.effect["level2_effect" + (j+1).toString() + "_mod"])
                      highestString = char.effect["level2_effect" + (j+1).toString() + "_mod"]
                    }
                  }
                }
              }else{
                if(this.effectLevel == false){
                  if((parseInt(char.effect["level1_effect" + (j+1).toString() + "_mod"])) > highestValue){
                    highestValue = parseInt(char.effect["level1_effect" + (j+1).toString() + "_mod"])
                    highestString = char.effect["level1_effect" + (j+1).toString() + "_mod"]
                  }
                }else{
                  if((parseInt(char.effect["level2_effect" + (j+1).toString() + "_mod"])) > highestValue){
                    highestValue = parseInt(char.effect["level2_effect" + (j+1).toString() + "_mod"])
                    highestString = char.effect["level2_effect" + (j+1).toString() + "_mod"]
                  }
                }
              }
            }
          }
        }

        if(Object.keys(char.repeat_effect).length != 0){
          for(let j = 0; j < char.repeat_effect.n_effects; j++){
            if(char.repeat_effect["effect" + (j+1).toString()].toLowerCase().replace(/ /g, "_") == query.ef){
              if(query.ef_target){
                if(char.repeat_effect.effect_who.toLowerCase() == query.ef_target){
                  if(this.effectLevel == false){
                    if((parseInt(char.repeat_effect["effect" + (j+1).toString() + "_value"])) > highestValue){
                      highestValue = parseInt(char.repeat_effect["effect" + (j+1).toString() + "_value"])
                      highestString = char.repeat_effect["effect" + (j+1).toString() + "_value"]
                    }
                  }else{
                    if((parseInt(char.repeat_effect["effect" + (j+1).toString() + "_mod"])) > highestValue){
                      highestValue = parseInt(char.repeat_effect["effect" + (j+1).toString() + "_mod"])
                      highestString = char.repeat_effect["effect" + (j+1).toString() + "_mod"]
                    }
                  }
                }
              }else{
                if(this.effectLevel == false){
                  if((parseInt(char.repeat_effect["effect" + (j+1).toString() + "_value"])) > highestValue){
                    highestValue = parseInt(char.repeat_effect["effect" + (j+1).toString() + "_value"])
                    highestString = char.repeat_effect["effect" + (j+1).toString() + "_value"]
                  }
                }else{
                  if((parseInt(char.repeat_effect["effect" + (j+1).toString() + "_mod"])) > highestValue){
                    highestValue = parseInt(char.repeat_effect["effect" + (j+1).toString() + "_mod"])
                    highestString = char.repeat_effect["effect" + (j+1).toString() + "_mod"]
                  }
                }
              }
            }
          }
        }
        return highestString
      }
    },
    chooseStyle(char){
      switch(this.displayColor){
        case ("char_time_limited"):
          if(char.time_limited == true){
            return {color: "red", fontWeight: "bold"}
          }else{
            return {color: "white"}
          }
        case ("char_free_unit"):
          if(char.is_free == true){
            return {color: "red", fontWeight: "bold"}
          }else{
            return {color: "white"}
          }
        default:
          return {color: "white"}
      }
    },
    handleUpdateSelect(){
      let filter = {
        // Char 
        char: this.selectedChar[0],
        star: this.selectedChar[1],
        status: this.selectedChar[2],
        timelimited: this.selectedChar[3],
        free: this.selectedChar[4],
        repeat: this.selectedChar[5],
        // Effect
        ef_target: this.selectedEffects[0],
        ef: this.selectedEffects[1],
        ef_val1: this.selectedEffects[2],
        ef_val2: this.selectedEffects[3],
        // Repeat
        n_repeat: this.selectedRepeat[0],
        // Only effect or repeat
        filter_repeat_effect: this.selectedIncludeRepeat[0],
        effect_lv: this.effectLevel ? true : null,

        sort: this.sortBy,
        asc: this.sortAsc,
        display: this.displayInfo
      }

      if(this.selectedEffects[1]){
        if(this.selectedEffects[1].indexOf("turns") > -1){
          console.log(1)
          this.effectTurns = true
        }else{
          this.effectTurns = false
        }
      }

      Object.keys(filter).forEach(key => {
        if (filter[key] === null || filter[key] === undefined) {
          delete filter[key];
        }
      });
      this.$router.replace({ name: 'filter_ast', query: filter })
    },
    getImg(url){
      return require('../assets/assists/hex_ast/' + url + '.png')
    },
    goBackChar(){
      this.$router.push({ name: "assist_list"});
    },
    gotoCharPage(char_id){
      this.$router.push({ name: "assist", params: {id: char_id} });
    },
    clearFilter(){
      // Char
      this.selectedChar[0] = null
      this.selectedChar[1] = null
      this.selectedChar[2] = null
      this.selectedChar[3] = null
      this.selectedChar[4] = null
      this.selectedChar[5] = null
      // Effect
      this.selectedEffects[0] = null
      this.selectedEffects[1] = null
      this.selectedEffects[2] = null
      this.selectedEffects[3] = null
      this.selectedCombination = null
      // Repeat
      this.selectedRepeat[0] = null
      // Only effect or repeat
      this.selectedIncludeRepeat[0] = null
      this.effectLevel = false

      this.sortBy = null
      this.sortAsc = null
      this.displayInfo = null

      this.$router.replace({'query': null});
    },
    filterChar(char, query){
      let temp = true;

      temp &&= (query.char ? char.name.toLowerCase().replace(/ /g, "_").replace("(", "").replace(")","") == query.char : true)
      temp &&= (query.star ? char.stars.toString() == query.star : true)
      temp &&= (query.timelimited ? char.time_limited.toString() == (query.timelimited) : true)
      temp &&= (query.free ? char.is_free.toString() == query.free : true)
      temp &&= (query.repeat ? char.have_repeat.toString() == query.repeat : true)

      if(query.status){
        let temp2 = false;
        switch(query.status){
          case "free_all":
            temp2 ||= char.type_stat == "free"
            temp2 ||= char.type_stat == "free_plus"
            break;
          case "str_all":
            temp2 ||= char.type_stat == "str"
            temp2 ||= char.type_stat == "str_plus"
            break;
          case "end_all":
            temp2 ||= char.type_stat == "end"
            temp2 ||= char.type_stat == "end_plus"
            break;
          case "dex_all":
            temp2 ||= char.type_stat == "dex"
            temp2 ||= char.type_stat == "dex_plus"
            break;
          case "agi_all":
            temp2 ||= char.type_stat == "agi"
            temp2 ||= char.type_stat == "agi_plus"
            break;
          case "mag_all":
            temp2 ||= char.type_stat == "mag"
            temp2 ||= char.type_stat == "mag_plus"
            break;
          default:
            temp2 ||= char.type_stat == query.status
            break;
        }
        temp &&= temp2 
      }
      return temp
    },
    filterEffect(char, query){
      if(query.ef_target || query.ef || query.ef_val1 || query.ef_val2 || query.filter_repeat_effect){
        let temp = false;
        if(Object.keys(char.effect).length != 0){
          for (let i = 0; i < char.effect.n_effects; i++){
            let temp2 = true
            temp2 &&= (query.ef_target ? char.effect["level1_effect" + (i+1).toString() + "_target"].toLowerCase() == query.ef_target : true)
            temp2 &&= (query.ef ? char.effect["level1_effect" + (i+1).toString() + "_attr"].toLowerCase().replace(/ /g, "_") == query.ef : true)
            if(this.effectLevel == false){
              temp2 &&= (query.ef_val1 ? parseInt(char.effect["level1_effect" + (i+1).toString() + "_mod"]) >= parseInt(query.ef_val1) : true)
              temp2 &&= (query.ef_val2 ? parseInt(char.effect["level1_effect" + (i+1).toString() + "_mod"]) <= parseInt(query.ef_val2) : true)
            }else{
              temp2 &&= (query.ef_val1 ? parseInt(char.effect["level2_effect" + (i+1).toString() + "_mod"]) >= parseInt(query.ef_val1) : true)
              temp2 &&= (query.ef_val2 ? parseInt(char.effect["level2_effect" + (i+1).toString() + "_mod"]) <= parseInt(query.ef_val2) : true)
            }
            temp ||= temp2
          }
          if(query.filter_repeat_effect == "atk"){
            // Filter and get chars with effect in normal action
            return temp
          }
        } else {
          if(query.filter_repeat_effect == "atk"){
            // Filter and get chars with effect in normal action
            return temp
          }
        }
        let temprepeat = false;
        if(Object.keys(char.repeat_effect).length != 0){
          for (let j = 0; j < char.repeat_effect.n_effects; j++){
            let temp2 = true
            temp2 &&= (query.ef_target ? char.repeat_effect["effect_who"].toLowerCase() == query.ef_target : true)
            temp2 &&= (query.ef ? char.repeat_effect["effect" + (j+1).toString()].toLowerCase().replace(/ /g, "_") == query.ef : true)
            if(this.effectLevel == false){
              temp2 &&= (query.ef_val1 ? parseInt(char.repeat_effect["effect" + (j+1).toString() + "_value"]) >= parseInt(query.ef_val1) : true)
              temp2 &&= (query.ef_val2 ? parseInt(char.repeat_effect["effect" + (j+1).toString() + "_value"]) <= parseInt(query.ef_val2) : true)
            }else{
              temp2 &&= (query.ef_val1 ? parseInt(char.repeat_effect["effect" + (j+1).toString() + "_mod"]) >= parseInt(query.ef_val1) : true)
              temp2 &&= (query.ef_val2 ? parseInt(char.repeat_effect["effect" + (j+1).toString() + "_mod"]) <= parseInt(query.ef_val2) : true) 
            }
            temprepeat ||= temp2
          }
          if(query.filter_repeat_effect == "repeat"){
            return temprepeat
          }
        } else {
          if(query.filter_repeat_effect == "repeat"){
            return temprepeat
          }
        }

        return temp ||= temprepeat
      }else{
        return true
      }
    },
    filterRepeat(char, query){
      let temp = true
      if(query.n_repeat){
        if(Object.keys(char.repeat_effect).length != 0){
          let temp2 = false
          if(this.effectLevel == false){
            temp2 ||= (query.n_repeat ? char.repeat_effect.howmany == query.n_repeat : true)
            
          }else{
            temp2 ||= (query.n_repeat ? char.repeat_effect.howmany2 == query.n_repeat : true)
            
          }

          temp &&= temp2
          return temp
        }else{
          return false
        }
      }else{
        return true
      }
    },
    searchFilter(filtered = this.dataAst){
      // console.log(this.effectLevel)
      this.filterModal = false;
      let query = this.$route.query;

      let characters = filtered.filter((char) => {
        let filt = this.filterChar(char, query)
       
        filt &&= this.filterEffect(char, query)

        filt &&= this.filterRepeat(char, query)

        return (filt)
      })

      let temp_char
      if(query.sort){
        if(query.asc){
            if(query.asc == "ascending"){
              temp_char = characters.sort((a, b) => a[query.sort] - b[query.sort])
            }else{
              temp_char = characters.sort((a, b) => b[query.sort] - a[query.sort])    
            }
        }else{
          temp_char = characters.sort((a, b) => b[query.sort] - a[query.sort])
        }
      }else{
        temp_char = characters
      }
      console.log(temp_char.length)
      this.isFirstSearch = false;
      this.filteredData = temp_char
    },
    decideBase(star){
      if(!false){
        switch (star) {
          case 1:
            return require('../assets/frame/frame_1.png')
          case 2:
            return require('../assets/frame/frame_2.png')
          case 3:
            return require('../assets/frame/frame_3.png')  
          case 4:
            return require('../assets/frame/frame_4_star.png')
          default:
            return require('../assets/frame/frame_1.png')    
        }
      }
    },
    decideBaseFrame(star){
      if(!false){
        switch (star) {
          case 1:
            return require('../assets/frame/base_frame_1.png')
          case 2:
            return require('../assets/frame/base_frame_2.png')
          case 3:
            return require('../assets/frame/base_frame_3.png')  
          case 4:
            return require('../assets/frame/base_frame_4_star.png')
          default:
            return require('../assets/frame/base_frame_1.png')    
        }
      }
    },
    topFunction(){
      window.scrollTo({
        top: 0,
        behavior: "smooth"
      });
    }
  },
}
</script>

